import React from 'react';
import { Article } from '../../components/Article';
import styles from './Article.module.scss';

export type task = {
  id: string,
  name: string,
  title?: string,
  description?: string,
  language?: string,
  code?: string,
}

export const ProgramTasks: React.FC = () => {
  const jsonFile: task[] = require('../../assets/jsons/code.json')
  return (
    <>
      {jsonFile.map((task: task) => {
        return (
          <Article {...task} />
        )
      })}
    </>
  )
}
